
.hov-anim {
    z-index: 1;
    position: relative;
    overflow: hidden;
    transition: color 0.4s ease-in-out;
}
.hov-anim::before {
    content: "";
    z-index: -1;
    position: absolute;
    bottom: 100%;
    left: 100%;
    width: 10em;
    height: 10em;
    border-radius: 50%;
    background-color: #eb88e6;
    transform-origin: center;
    transform: translate3d(-50%, 50%, 0) scale3d(0, 0, 0);
    transition: transform 0.45s ease-in-out;
}
.hov-anim:hover::before {
    transform: translate3d(-50%, 50%, 0) scale3d(15, 15, 15);
}
